import { html } from 'lit';

// Import Shoelace components
import '@shoelace-style/shoelace/dist/components/button/button.js';
import '@shoelace-style/shoelace/dist/components/icon/icon.js';

// More on how to set up stories at: https://storybook.js.org/docs/web-components/writing-stories/introduction
export default {
  title: 'Shoelace/Button',
  tags: ['autodocs'],
  render: (args) => html`
    <sl-button
      variant=${args.variant}
      size=${args.size}
      ?disabled=${args.disabled}
      ?loading=${args.loading}
      ?outline=${args.outline}
      ?pill=${args.pill}
      ?circle=${args.circle}
      ?caret=${args.caret}
    >
      ${args.prefix ? html`<sl-icon slot="prefix" name=${args.prefix}></sl-icon>` : ''}
      ${args.label}
      ${args.suffix ? html`<sl-icon slot="suffix" name=${args.suffix}></sl-icon>` : ''}
    </sl-button>
  `,
  argTypes: {
    variant: {
      control: 'select',
      options: ['default', 'primary', 'success', 'neutral', 'warning', 'danger', 'text']
    },
    size: {
      control: 'select',
      options: ['small', 'medium', 'large']
    },
    disabled: { control: 'boolean' },
    loading: { control: 'boolean' },
    outline: { control: 'boolean' },
    pill: { control: 'boolean' },
    circle: { control: 'boolean' },
    caret: { control: 'boolean' },
    prefix: { control: 'text' },
    suffix: { control: 'text' },
    label: { control: 'text' }
  }
};

// Default button
export const Default = {
  args: {
    label: 'Button',
    variant: 'default'
  }
};

// Primary button
export const Primary = {
  args: {
    label: 'Primary',
    variant: 'primary'
  }
};

// Success button
export const Success = {
  args: {
    label: 'Success',
    variant: 'success'
  }
};

// With icon
export const WithIcon = {
  args: {
    label: 'Settings',
    prefix: 'gear'
  }
};

// Loading state
export const Loading = {
  args: {
    label: 'Loading',
    loading: true
  }
};

// Pill button
export const Pill = {
  args: {
    label: 'Pill Button',
    pill: true
  }
};

// Circle icon button
export const Circle = {
  args: {
    circle: true,
    prefix: 'gear',
    label: ''
  }
}; 